import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import java from '../assets/images/about-img.jpg';
import basic from '../assets/images/about-img2.jpg';

class About extends React.Component {
  render() {
    return (
      <section id="about">
      <div className="container">
      <div className="row">
      <div className="col-md-12">

      <div className="welcome-area">
      <div className="title-area">
      <h2 className="tittle">  <span>Neolabs</span></h2>
      <span className="tittle-line"></span>
      <p>
      ,      !!!  ,         ,         .       NeoLabs -        .

      </p>
      </div>
      </div>
      </div>
      </div>
      <div className="row">
      <div className="col-md-12">
      <div className="about-area">
      <div className="row">
      <div className="col-md-5 col-sm-6 col-xs-12">
      <div className="about-left wow fadeInLeft">
      <img src={java} alt="img"/>

      </div>
      </div>
      <div className="col-md-7 col-sm-6 col-xs-12">
      <div className="about-right wow fadeInRight">
      <div className="title-area">
      <h2 className="tittle"> <span> Java </span>    <span> </span></h2>
      <span className="tittle-line"></span>
      <p>
      ,      ! Neobis        ,   !  ,       ,      !            ,       JAVA  Android      .          . ,  ,  ,     .        ,        !
      </p>
      <div className="about-btn-area">
      <Link to="/neolabs/java" className="button button-default" data-text=""><span></span></Link>
      </div>                    
      </div>
      </div>
      </div>
      </div>
      </div>
      </div>
      </div>
      <div className="row">
      <div className="col-md-12">
      <div className="about-area">
      <div className="row">
      <div className="col-md-7 col-sm-6 col-xs-12">
      <div className="about-right wow fadeInLeft">
      <div className="title-area">
      <h2 className="tittle">  <span>   </span></h2>
      <span className="tittle-line"></span>
      <p>
      ,      !!!  ,         ,         .         .       ,          : 
      -    Android
      -    iOS (iPhone)
      -  
      -  
      -      
      - UX/UI     
      -  IT  
      </p>
      <div className="about-btn-area">
      <a href="#" className="button button-default" data-text=""><span></span></a>
      </div>                    
      </div>
      </div>
      </div>
      <div className="col-md-5 col-sm-6 col-xs-12">
      <div className="about-left wow fadeInRight">
      <img src={basic} alt="img"/>

      </div>
      </div>
      </div>
      </div>
      </div>
      </div>
      </div>
      </section> 
      )
  }
}

export default About;


